.popup__input {
  font-family: 'Inter', Arial, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #000;
  box-sizing: border-box;
  width: 100%;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  /* margin-bottom: 30px; */
  padding-bottom: 8px;
  outline: none;
}

@media screen and (max-width: 767px) {
  .popup__profile-title-input {
    font-size: 14px;
    line-height: 17px;
  }
}