.header__logo {
  width: 142px;
  height: 33px;
  background-image: url("/src/images/logo.svg");
  box-sizing: border-box;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

@media screen and (max-width: 423px) {
  .header__logo {
    width: 103.74px;
    height: 24.4px;
  }
}